.Furniture{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.Furniture__image{
    height: 100%;
    width: 100%;
    z-index: -1;
    mask-image: linear-gradient(to top, transparent 15%, black 25%);
}
.Furniture1{
    font-family: Algerian;
    font-weight: 600;
    font-size: 40px;
    background-color: rgb(207, 28, 28);
    color: white;
    width: 100%;
    text-align: center;
}
.Furniture__row1{
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
    margin-top: -90px;
}